.project-line {
    writing-mode: vertical-rl;
    text-orientation: upright;
    font-size: 2rem;
    font-weight: bold;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.title1{
    margin-top: 30px;
    margin-bottom: 20px;
    font-size: 50px;
    font-weight: bold;
}

.projects-section {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    width: 100%;
}

.projects-wrapper {
    width: 85%;
    display: flex;
    flex-direction: column;
    align-items: flex-end; 
    margin-left: auto; 
}

.project-sidebar {
    width: 15%; 
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height: 100vh; 
}

.project-card {
    display: flex;
    width: 90%;
    height: 400px;
    border: 2px solid black;
    margin: 0;
    overflow: hidden;
    justify-content: flex-end;
}


.project-card:nth-child(even) {
    flex-direction: row-reverse;
}

.project-image {
    width: 97%;
    height: 100%;
    object-fit: cover;
}

.project-details {
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    text-align: right;
    background: white;
}

.project-title1 {
    font-size: 14px;
    font-weight: bold;
    margin: 10px;
}
.view-details-btn {
    width: 100%;
    background-color: black;
    color: white;
    border: none;
    padding: 15px;
    cursor: pointer;
    margin: 0;
    align-self: stretch; 
}

.view-details-btn:hover {
    background-color: #333;
}