.navbar-container {
  direction: rtl;
  text-align: right;
  border-bottom: 1px solid black;
}
.navbar-top {
  height: 10px;
  background-color: black;
}
.navbar-top-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  position: relative;
  border-bottom: 2px solid black;
  height: 100px;

}

.logo img {
  height: 50px;
}

.nav-icons {
  display: flex;
  align-items: center;
  gap: 15px;
}

.nav-icons img {
  height: 25px;
  cursor: pointer;
}

.navbar-bottom-section {
  text-align: right;
  height: 70px;
}

.nav-links {
  list-style: none;
  display: flex;
  justify-content: right;
  gap: 220px;
  height: 50px;
  align-items: end;
  padding-right: 0;

}

.nav-links li {
  position: relative;
  display: flex;
  align-items: end;
  height: 100%; 
  padding: 0 15px; 
}

.nav-links li:not(:first-child)::before {
  content: "";
  width: 1px;
  height: 80%; 
  background-color: black;
  position: absolute;
  right: 0;
  top: 0; 
}
.nav-links a {
  color: black;
  text-decoration: none;
  font-size: 1rem;
  font-weight: bold;
  padding-bottom: 5px;
  border-bottom: 2px solid transparent; 
}
.nav-links a.active-link {
  border-bottom: 2px solid black; 
}
.dropdown {
  position: relative;
}

.dropdown-content {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  background-color: white;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  min-width: 300px;
  z-index: 10;
  padding: 10px;
  display: flex;
  gap: 20px;
}

.dropdown:hover .dropdown-content {
  display: flex;
}

.column {
  display: flex;
  flex-direction: column;
  width: 200px;
}

.column h4 {
  font-size: 1.2rem;
  font-weight: bold;
  font-size: 14px !important;
}

.dropdown-content a {
  display: block;
  color: black;
  padding: 10px;
  text-decoration: none;
  font-size: 0.9rem;
}

.dropdown-content a:hover {
  background-color: #f2f2f2;
}

.category {
  position: relative;
}

.dropdown-absolute {
  position: absolute;
  top: 0;
  right: 100%;
  background-color: white;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  min-width: 200px;
  display: none;
}

.category:hover .dropdown-absolute {
  display: block;
}

.dropdown-absolute a {
  display: block;
  color: black;
  padding: 8px;
  text-decoration: none;
}

.dropdown-absolute a:hover {
  background-color: #ddd;
}
